"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import { AppShell } from "@/components/app-shell"
import { AssemblyMetricCard } from "@/components/AssemblyMetricCard"
import { LiveAgentActivity } from "@/components/LiveAgentActivity"
import { AnimatedJobsTable } from "@/components/AnimatedJobsTable"
import { MetricCardSkeleton, JobsTableSkeleton } from "@/components/skeleton-loaders"
import { Card } from "@/components/ui/card"
import { AuditReadyBadge } from "@/components/audit-ready-badge"
import { PipelineVisualizer } from "@/components/PipelineVisualizer"
import { dashboardMetrics, mockJobs, metricTooltips } from "@/lib/mock-data"
import { Users, Target, Briefcase, AlertTriangle } from "lucide-react"

export default function DashboardPage() {
  const router = useRouter()
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    const timer = setTimeout(() => setIsLoading(false), 1200)
    return () => clearTimeout(timer)
  }, [])

  const recentJobs = mockJobs.slice(0, 4)

  return (
    <>
      <AppShell>
        <div className="space-y-8">
          {/* Header */}
          <div className="flex flex-col gap-1 sm:flex-row sm:items-center sm:justify-between">
            <div>
              <h1 className="text-2xl font-bold text-balance">Provider Validation Dashboard</h1>
              <p className="text-muted-foreground">
                Real-time overview of your healthcare provider data validation pipeline
              </p>
            </div>
            <AuditReadyBadge />
          </div>

          <p className="text-sm text-muted-foreground border-l-2 border-primary/30 pl-3">
            Each provider record is validated by multiple AI agents in parallel, cross-referencing government registries,
            state licensing boards, and verified address databases.
          </p>

          {/* Metrics Grid with Assembly Animation */}
          <div className="grid gap-4 sm:grid-cols-2 lg:grid-cols-4">
            {isLoading ? (
              <>
                <MetricCardSkeleton />
                <MetricCardSkeleton />
                <MetricCardSkeleton />
                <MetricCardSkeleton />
              </>
            ) : (
              <>
                <AssemblyMetricCard
                  title="Total Providers Validated"
                  value={dashboardMetrics.totalProvidersValidated.toLocaleString()}
                  change={dashboardMetrics.totalProvidersChange}
                  changeLabel="vs last month"
                  icon={<Users className="h-5 w-5 text-muted-foreground" />}
                  tooltip={metricTooltips.totalProviders}
                  index={0}
                />
                <AssemblyMetricCard
                  title="Average Confidence Score"
                  value={`${dashboardMetrics.averageConfidenceScore}%`}
                  change={dashboardMetrics.confidenceChange}
                  changeLabel="vs last month"
                  icon={<Target className="h-5 w-5 text-muted-foreground" />}
                  tooltip={metricTooltips.averageConfidence}
                  index={1}
                />
                <AssemblyMetricCard
                  title="Active Jobs"
                  value={dashboardMetrics.activeJobs}
                  change={dashboardMetrics.activeJobsChange}
                  changeLabel="vs yesterday"
                  icon={<Briefcase className="h-5 w-5 text-muted-foreground" />}
                  tooltip={metricTooltips.activeJobs}
                  index={2}
                />
                <AssemblyMetricCard
                  title="Pending Manual Review"
                  value={dashboardMetrics.recordsRequiringReview}
                  change={dashboardMetrics.reviewChange}
                  changeLabel="vs last week"
                  icon={<AlertTriangle className="h-5 w-5 text-muted-foreground" />}
                  tooltip={metricTooltips.recordsReview}
                  index={3}
                />
              </>
            )}
          </div>

          {/* NEW: Live Agent Activity Section */}
          {!isLoading && (
            <Card className="bg-card/50 border-border">
              <LiveAgentActivity />
            </Card>
          )}

          {/* Recent Jobs with Assembly Animation */}
          {isLoading ? (
            <Card className="bg-card border-border p-6">
              <JobsTableSkeleton rows={4} compact />
            </Card>
          ) : (
            <AnimatedJobsTable jobs={recentJobs} />
          )}
        </div>
      </AppShell>

      {/* Pipeline Visualizer - Vertical Timeline */}
      <PipelineVisualizer />
    </>
  )
}
